var modals = [];
module.exports = {
  add: function add(element) {
    if (modals.indexOf(element) === -1) {
      modals.push(element);
    }
  },
  remove: function remove(element) {
    var index = modals.indexOf(element);

    if (index === -1) {
      return;
    }

    modals.splice(index, 1);
  },
  count: function count() {
    return modals.length;
  }
};